.header-library {
  margin-right: auto;
  margin-left: auto;
  //   background-image: url(../image);

  @media screen and (min-width: 320px) {
    height: 230px;
  }
  background: rgba(0, 0, 0, 0.56);
  background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.56),
      rgba(0, 0, 0, 0.56)
    ),
    url(../images/bg-1x-mob-library.jpg);
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url(../images/bg-2x-mob-library.jpg);
  }
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: black;

  @media screen and (min-width: 768px) {
    height: 216px;
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url(../images/bg-1x-tablet-library.jpg);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url(../images/bg-2x-tablet-library.jpg);
    }
  }

  @media screen and (min-width: 1280px) {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url(../images/bg-1x-desctop-library.jpg);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url(../images/bg-2x-desctop-library.jpg);
    }
  }
}
.header-library__links {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.header-library__tittle {
  display: none;
  margin: 0;
  @media screen and (min-width: 768px) {
    margin-left: 10px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 1.17;
    color: #ffffff;
    display: flex;
  }
}
.header-library__link {
  position: relative;
  color: #fff;
  text-decoration: none;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.17;
  text-transform: uppercase;
  margin-left: auto;
  white-space: nowrap;
}
.header-library__link:last-child {
  margin-left: 5px;
}
.header-library__btn-list {
  display: flex;
  justify-content: space-evenly;
  @media screen and (min-width: 768px) {
    justify-content: center;
  }
}
.header-library__btn {
  padding: 14px 36px 12px 36px;
  // margin-top: 60px;

  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-transform: uppercase;
  color: #ffffff;
  width: 129.53px;
  height: 44px;
  border-radius: 5px;
  background-color: transparent;
  border-color: #fff;
  cursor: pointer;
  @media screen and (min-width: 768px) {
    // margin-top: 40px;
    width: 152px;
    height: 44px;
    margin-right: 32px;
  }
  @media screen and (min-width: 1280px) {
    width: 147px;
    height: 42px;
    margin-right: 20px;
  }
}
.header-library__btn:last-child {
  margin-right: 0;
}
.header-library__btn:hover {
  background-color: getColor('secondary-text-color');
  border: none;
}
.header-library__btn:focus {
  background-color: getColor('secondary-text-color');
  border: none;
}
.header-library__btn-active {
  background-color: getColor('secondary-text-color');
  border: none;
}

.nav-library {
  display: flex;
  align-items: center;
  padding: 40px 20px;
    @include media-min(mobile) {
      padding: 40px;
      }
}
.header-library__list {
  display: flex;
  margin-left: auto;
  align-items: center;
  gap: 30px;
    @include media-min(mobile) {
        gap: 39px;
      }
}
.nav-library__logo {
  display: flex;
  align-items: center;
}
.site-nav__link--current::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -2px;
  display: block;
  width: 100%;
  height: 3px;
  background-color: #ff001b;
  border-radius: 2px;
  opacity: 1;
}
.movie-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  @include media-min(tablet) {
    gap: 32px;
  }
  @include media-min(desktop) {
    gap: 16px;
  }
}
.library__container {
  padding-top: 20px;
  padding-bottom: 40px;
  @include media-min(tablet) {
    padding-left: 32px;
    padding-right: 32px;
    padding-top: 60px;
    padding-bottom: 60px;
  }
}
.photo__card {
  display: flex;
  flex-direction: column;
}
.movie__image {
  height: auto;
}
.movie__vote-average {
  width: 36px;
  height: 16px;
  border-radius: 5px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.17;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: getColor('secondary-text-color');
  color: getColor('white');
  @include media-min(desktop) {
    width: 40px;
    height: 20px;
  }
}
.movie__title {
  text-transform: uppercase;
  // color: getColor("white");
  font-weight: 500;
  font-size: 20px;
  line-height: calc(24 / 20);
}
.header-library__auth-box {
  position: absolute;
  top: 27px;
  left: 34%;
  transform: translate(-50%);
  display: flex;
  gap: 5px;
  align-items: center;
    @include media-min(mobile) {
        top: 23px;
      }
}
.header-library__user-name {
  font-weight: 500;
  color: #ff6b01;
}

.header-library__logout-btn {
  cursor: pointer;
  fill: #ff6b01;
  transition: transform 0.25s linear, visibility 0.25s linear, fill 0.35s linear;
  &:hover,
  &:focus {
    transform: scale(1.1);
    fill: #fff;
  }
}

.not-img-stile {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.not-img {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  position: absolute;
}
